import { useState } from "react";
import user from "./images/user.jpg";
import "./App.css";
import Social from "./component/Social";
import Display from "./component/Display";
import Button from "./component/Button";
import Like from "./component/Like";

function App() {
  const [count, setCount]=useState(0)
  const handleCount = () => {
    setCount(count + 1);
  };
  const [skills, setSkills] = useState();

  const handleGivenName = () => {
    setSkills("My Name is Natsnet");
  };

  const handleYourCourseTrack = () => {
    setSkills("I'm a Frontend Developer");
  };

  return (
    <>
      <main className="container">
        <header className="card">
          <img src={user} alt="user" />
        </header>
        <h3>Hi!</h3>
        <br></br>
        <div className="styling">
          <Display tech={skills} />
          <div className="btns">
            <Button clickHandler={handleGivenName} btntext={"Name"} />
            <Button clickHandler={handleYourCourseTrack} btntext={"Skills"} />
          </div>
        </div>
        <Display tech={count} />
        <div className="like">
          <Like clickHandler={handleCount} />
        </div>
        <br></br>
        <Social />
      </main>
    </>
  );
}

export default App;
